import Vue from 'vue';
import Vuex from 'vuex';
import mutations from "./mutations";
import actions from "./actions";

Vue.use(Vuex);

const state = {
  isInit: false,        // 
  lock: false,          // 
  audio: null,          // 
  isPlay: false,        // 

  playlists: [],        // 
  newSonglist: [],      // 
  songlistDetails: {},  // 

  duration: 0,          // 
  currentTime: 0,       // 
  currentSong: null,    // 
  currentLyric: {},     // 
  currentPlayIndex: 0,  // index
  showPageIndex: 1,     // playPageindexplayPage
  topPageIndex: -1,     // index
  middlePageIndex: -1,  // index
  bottomPageIndex: -1,  // index
  currentPlayQueue: [], // 

  cacheQueue: new Map(),// 
  recentPlaySongs: [],  // 
  colSongs: {},         // 
  colPlaylists: {},     // 
};

const store = new Vuex.Store({
  state,
  mutations,
  actions
});

export default store;